import React from 'react';
import { Button, Col, Container, Row, Tab, Tabs } from 'react-bootstrap';
import '../scss/detail.scss'
import { useNavigate, useParams } from 'react-router-dom';
import { addItem } from "./../store.js";
import { useDispatch } from 'react-redux';

const Detail = (props) => {
  const { all } = props;
  const { id } = useParams()
  const navigate=useNavigate()
  let dispatch = useDispatch()



  return (
    <>
    
    <Container className='detail'>
      <Row>
        <Col md={5}>
          <img src={process.env.PUBLIC_URL +all[id].imgUrl} alt="" className='img1' />
        </Col>
        <Col md={7} className='txt_box'>
          <h3>{all[id].title}</h3>
          <h4>{all[id].subTitle}</h4>
          <p className='price'>{all[id].price}원 (10% sael)</p>

          <div className='size'>
            <p>SIZE</p>
            <Button variant="danger">001</Button>
          </div>

          <div className='pick'>
            <p>추천 상품</p>
            <img src={process.env.PUBLIC_URL +"/img/bag4.jpg"} alt="" />
            <img src={process.env.PUBLIC_URL +"/img/pr3.jpg"} alt="" />
            <img src={process.env.PUBLIC_URL +"/img/s5.jpg"} alt="" />
          </div>

          <div className="buy_box mt-4">
            <p>총 상품금액(수량) :</p>
            <span> {all[id].price}원 (1개)</span>
          </div>

          <div className="buy">
            <Button variant="outline-danger"  onClick={()=>{dispatch(addItem({img: all[id].imgUrl, price:all[id].price,  item: all[id].title, amount: 1 }))}}>ADD to CART</Button>
            <Button variant="outline-danger">BUY NOW</Button>
          </div>

        </Col>

      </Row>

    </Container>

    <Container className="detail_tab mt-5">
          <Tabs
            defaultActiveKey="profile"
            id="uncontrolled-tab-example"
            className="mb-3"
          >
            <Tab eventKey="home" title="상품 정보" >
              <img src={process.env.PUBLIC_URL +all[id].detail_} alt="" />
            </Tab>
            <Tab eventKey="profile" title="교환 및 반품" >
              <p className='notice mt-4'>
              <span>교환 및 반품이 가능한 경우</span> <br />
              - 상품을 공급 받으신 날로부터 7일이내 단, 가전제품의
              경우 포장을 개봉하였거나 포장이 훼손되어 상품가치가 상실된 경우에는 교환/반품이 불가능합니다. <br />
              - 공급받으신 상품 및 용역의 내용이 표시.광고 내용과
              다르거나 다르게 이행된 경우에는 공급받은 날로부터 3월이내, 그사실을 알게 된 날로부터 30일이내 <br />
              <br />
              <span>교환 및 반품이 불가능한 경우</span> <br />
              - 고객님의 책임 있는 사유로 상품등이 멸실 또는 훼손된 경우. 단, 상품의 내용을 확인하기 위하여 포장 등을 훼손한 경우는 제외 <br />
              - 포장을 개봉하였거나 포장이 훼손되어 상품가치가 상실된 경우
              (예 : 가전제품, 식품, 음반 등, 단 액정화면이 부착된 노트북, LCD모니터, 디지털 카메라 등의 불량화소에 따른 반품/교환은 제조사 기준에 따릅니다.) <br />
              - 고객님의 사용 또는 일부 소비에 의하여 상품의 가치가 현저히 감소한 경우 단, 화장품등의 경우 시용제품을 제공한 경우에 한 합니다. <br />
              - 시간의 경과에 의하여 재판매가 곤란할 정도로 상품등의 가치가 현저히 감소한 경우 <br />
              - 복제가 가능한 상품등의 포장을 훼손한 경우 <br />
              <br />
              (자세한 내용은 고객만족센터 1:1 E-MAIL상담을 이용해 주시기 바랍니다.)
              <br />
              ※ 고객님의 마음이 바뀌어 교환, 반품을 하실 경우 상품반송 비용은 고객님께서 부담하셔야 합니다. (색상 교환, 사이즈 교환 등 포함)</p>
            </Tab>
            <Tab eventKey="contact" title="상품 리뷰" >
              <p className='text-center mt-5'>아직 작성된 리뷰가 없습니다.</p>
            </Tab>
          </Tabs>
          </Container>
    </>
  );
}

export default Detail;
